"use client";

import React, { useEffect, useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid";

/**
 * Chat-style UI between two local agents (localhost:3001 and localhost:3002)
 *
 * - Direct mode: the browser calls agents directly (requires CORS enabled on agents).
 * - This file is a drop-in replacement for pages/index.js (client component).
 *
 * No extra notes in the UI — just a chat app.
 */

const AGENTS = [
  { id: "agent-A", name: "Agent A", url: "http://localhost:3001", side: "left" },
  { id: "agent-B", name: "Agent B", url: "http://localhost:3002", side: "right" },
];

function nowIso() {
  return new Date().toISOString();
}

export default function Home() {
  const [fromAgentUrl, setFromAgentUrl] = useState(AGENTS[0].url);
  const [toAgentUrl, setToAgentUrl] = useState(AGENTS[1].url);
  const [text, setText] = useState("");
  const [messages, setMessages] = useState([]); // { id, fromUrl, toUrl, text, ts, status, meta }
  const [sending, setSending] = useState(false);
  const scrollRef = useRef(null);

  useEffect(() => {
    // auto scroll when messages change
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  // Helper: create message object and append
  function pushMessage(msg) {
    setMessages((m) => [...m, { ...msg }]);
  }

  // Send via direct agent endpoint
  async function sendMessage() {
    if (!text.trim()) return;
    setSending(true);
    const id = uuidv4();
    const outMsg = {
      id,
      fromUrl: fromAgentUrl,
      toUrl: toAgentUrl,
      text,
      ts: nowIso(),
      status: "sending",
    };
    pushMessage(outMsg);
    setText("");

    try {
      const body = { receiverUrl: toAgentUrl, payload: { text } };
      const sendUrl = `${fromAgentUrl.replace(/\/$/, "")}/send-request`;

      const res = await fetch(sendUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      // try parse JSON else text
      const txt = await res.text();
      let parsed;
      try { parsed = JSON.parse(txt); } catch { parsed = txt; }

      // update outgoing message status
      setMessages((m) => m.map(msg => msg.id === id ? { ...msg, status: "sent", meta: { proxied_status: res.status, proxied_body: parsed } } : msg));

      // Create a synthetic incoming message from receiver summarizing the response
      const incoming = {
        id: uuidv4(),
        fromUrl: toAgentUrl,
        toUrl: fromAgentUrl,
        text: typeof parsed === "string" ? parsed : JSON.stringify(parsed, null, 2),
        ts: nowIso(),
        status: "received",
      };
      pushMessage(incoming);
    } catch (err) {
      setMessages((m) => m.map(msg => msg.id === id ? { ...msg, status: "error", meta: { error: String(err) } } : msg));
      // also push an error message from system/receiver
      pushMessage({
        id: uuidv4(),
        fromUrl: "system",
        toUrl: fromAgentUrl,
        text: `Error sending: ${String(err)}`,
        ts: nowIso(),
        status: "error",
      });
    } finally {
      setSending(false);
    }
  }

  // quick helper to get agent display name
  function agentName(url) {
    const a = AGENTS.find(x => x.url === url);
    return a ? a.name : url;
  }

  // render a bubble (left/right) based on fromUrl
  function renderBubble(msg) {
    // system messages centered
    if (msg.fromUrl === "system") {
      return (
        <div key={msg.id} style={{ textAlign: "center", color: "#666", margin: "12px 0" }}>
          <small>{msg.text}</small>
        </div>
      );
    }

    const alignLeft = AGENTS.find(a => a.url === msg.fromUrl)?.side === "left";
    const container = {
      display: "flex",
      justifyContent: alignLeft ? "flex-start" : "flex-end",
      marginBottom: 8,
    };
    const bubble = {
      maxWidth: "72%",
      padding: "10px 14px",
      borderRadius: 12,
      background: alignLeft ? "#eef2ff" : "#d1fae5",
      border: "1px solid rgba(0,0,0,0.06)",
      whiteSpace: "pre-wrap",
      fontFamily: "monospace",
      fontSize: 14,
      lineHeight: 1.35,
    };
    const metaStyle = { fontSize: 11, color: "#666", marginTop: 6, textAlign: alignLeft ? "left" : "right" };

    return (
      <div key={msg.id} style={container}>
        <div style={bubble}>
          <div style={{ fontWeight: 600, marginBottom: 6 }}>{agentName(msg.fromUrl)}</div>
          <div>{msg.text}</div>
          <div style={metaStyle}>
            <span>{new Date(msg.ts).toLocaleTimeString()}</span>
            {" • "}
            <em>{msg.status}</em>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div style={{ height: "100vh", display: "flex", flexDirection: "column", background: "#fafafa", padding: 20, boxSizing: "border-box" }}>
      <header style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: 12 }}>
        <div style={{ fontWeight: 700, fontSize: 18 }}>AI Agent Chat</div>
        <div style={{ fontSize: 13, color: "#666" }}>
          {agentName(fromAgentUrl)} → {agentName(toAgentUrl)}
        </div>
      </header>

      <main style={{ flex: 1, display: "flex", gap: 20 }}>
        {/* Chat column */}
        <section style={{ flex: 1, display: "flex", flexDirection: "column", borderRadius: 8, background: "#fff", padding: 12, boxShadow: "0 1px 3px rgba(0,0,0,0.06)" }}>
          <div ref={scrollRef} style={{ overflowY: "auto", padding: "6px 8px", flex: 1 }}>
            {messages.length === 0 && (
              <div style={{ textAlign: "center", color: "#888", marginTop: 40 }}>No messages yet — send the first message.</div>
            )}
            {messages.map(m => renderBubble(m))}
          </div>

          <div style={{ marginTop: 12, display: "flex", gap: 10, alignItems: "flex-end" }}>
            <div style={{ display: "flex", flexDirection: "column", gap: 6 }}>
              <label style={{ fontSize: 12, color: "#444" }}>From</label>
              <select value={fromAgentUrl} onChange={e => setFromAgentUrl(e.target.value)} style={{ padding: "8px 10px", borderRadius: 6 }}>
                {AGENTS.map(a => <option key={a.url} value={a.url}>{a.name}</option>)}
              </select>
            </div>

            <div style={{ display: "flex", flexDirection: "column", gap: 6 }}>
              <label style={{ fontSize: 12, color: "#444" }}>To</label>
              <select value={toAgentUrl} onChange={e => setToAgentUrl(e.target.value)} style={{ padding: "8px 10px", borderRadius: 6 }}>
                {AGENTS.map(a => <option key={a.url} value={a.url}>{a.name}</option>)}
              </select>
            </div>

            <div style={{ flex: 1, display: "flex", flexDirection: "column" }}>
              <label style={{ fontSize: 12, color: "#444" }}>Message</label>
              <textarea
                value={text}
                onChange={e => setText(e.target.value)}
                rows={2}
                placeholder='Type a message e.g. "Hello, how are you?"'
                style={{ width: "100%", padding: 10, borderRadius: 8, resize: "none", fontFamily: "monospace" }}
                onKeyDown={(e) => {
                  if (e.key === "Enter" && !e.shiftKey) {
                    e.preventDefault();
                    sendMessage();
                  }
                }}
              />
            </div>

            <div style={{ display: "flex", flexDirection: "column", justifyContent: "flex-end" }}>
              <button onClick={sendMessage} disabled={sending} style={{ padding: "10px 14px", borderRadius: 8, background: "#2563eb", color: "#fff", border: "none", cursor: "pointer" }}>
                {sending ? "Sending…" : "Send"}
              </button>

              <button onClick={() => { setMessages([]); }} style={{ marginTop: 8, padding: "6px 8px", borderRadius: 6, background: "#eee", border: "none", cursor: "pointer" }}>
                Clear
              </button>
            </div>
          </div>
        </section>

        {/* Side column: quick controls & recent intents */}
        <aside style={{ width: 320, display: "flex", flexDirection: "column", gap: 12 }}>
          <div style={{ padding: 12, borderRadius: 8, background: "#fff", boxShadow: "0 1px 3px rgba(0,0,0,0.06)" }}>
            <div style={{ fontWeight: 600, marginBottom: 8 }}>Agents</div>
            {AGENTS.map(a => (
              <div key={a.id} style={{ display: "flex", justifyContent: "space-between", padding: "6px 0", borderBottom: "1px dashed #eee" }}>
                <div>
                  <div style={{ fontWeight: 600 }}>{a.name}</div>
                  <div style={{ fontSize: 12, color: "#666" }}>{a.url}</div>
                </div>
                <div style={{ alignSelf: "center" }}>
                  <button onClick={() => navigator.clipboard?.writeText(a.url)} style={{ padding: "6px 8px", borderRadius: 6, border: "none", background: "#f3f4f6", cursor: "pointer" }}>Copy</button>
                </div>
              </div>
            ))}
          </div>

          <div style={{ padding: 12, borderRadius: 8, background: "#fff", boxShadow: "0 1px 3px rgba(0,0,0,0.06)" }}>
            <div style={{ fontWeight: 600, marginBottom: 8 }}>Recent activity</div>
            <div style={{ fontSize: 13, color: "#444" }}>
              {messages.slice().reverse().slice(0, 8).map(m => (
                <div key={m.id} style={{ marginBottom: 8 }}>
                  <div style={{ fontSize: 12, color: "#666" }}>{new Date(m.ts).toLocaleTimeString()} — <strong>{m.fromUrl === "system" ? "System" : agentName(m.fromUrl)}</strong></div>
                  <div style={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis", maxWidth: 260 }}>{m.text}</div>
                </div>
              ))}
              {messages.length === 0 && <div style={{ color: "#888" }}>No activity yet.</div>}
            </div>
          </div>
        </aside>
      </main>
    </div>
  );

  // local helpers
  function agentName(url) {
    const a = AGENTS.find(x => x.url === url);
    return a ? a.name : url;
  }

  async function sendMessage() {
    if (!text.trim()) return;
    setSending(true);
    const id = uuidv4();
    const outMsg = {
      id,
      fromUrl: fromAgentUrl,
      toUrl: toAgentUrl,
      text,
      ts: nowIso(),
      status: "sending",
    };
    pushMessage(outMsg);
    setText("");

    try {
      const body = { receiverUrl: toAgentUrl, payload: { text } };
      const sendUrl = `${fromAgentUrl.replace(/\/$/, "")}/send-request`;

      const res = await fetch(sendUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      });

      const txt = await res.text();
      let parsed;
      try { parsed = JSON.parse(txt); } catch { parsed = txt; }

      setMessages((m) => m.map(msg => msg.id === id ? { ...msg, status: "sent", meta: { proxied_status: res.status, proxied_body: parsed } } : msg));

      const incoming = {
        id: uuidv4(),
        fromUrl: toAgentUrl,
        toUrl: fromAgentUrl,
        text: typeof parsed === "string" ? parsed : JSON.stringify(parsed, null, 2),
        ts: nowIso(),
        status: "received",
      };
      pushMessage(incoming);
    } catch (err) {
      setMessages((m) => m.map(msg => msg.id === id ? { ...msg, status: "error", meta: { error: String(err) } } : msg));
      pushMessage({
        id: uuidv4(),
        fromUrl: "system",
        toUrl: fromAgentUrl,
        text: `Error sending: ${String(err)}`,
        ts: nowIso(),
        status: "error",
      });
    } finally {
      setSending(false);
    }
  }
}
